import React from "react";
import styled from "styled-components";

import Button from "../Button";

import { MenuItems, SocialLinks } from "./MenuItems";

import HamburgerIcon from "./socialIcons/icon-hamburger.svg";

import CloseIcon from "./socialIcons/icon-close.svg";

import { Container } from "react-bootstrap";

import { useState } from "react";

const NavbarItems = styled.nav`
  background-color: transparent;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;

  @media screen and (max-width: 800px) {
    position: relative;
  }
`;

const NavbarLogo = styled.h1`
  color: #fff;
  justify-self: start;
  margin-left: 1.2rem;
  cursor: pointer;
`;

const MenuIcon = styled.div`
  display: none;
  img {
    color: #fff;
  }

  @media screen and (max-width: 800px) {
    display: inline-block;
    position: absolute;
    left: 2rem;
  }
`;

const Menu = styled.div`
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  @media screen and (max-width: 800px) {
    ${"" /* position: relative; */}

    flex-direction: column;
    visibility: hidden;
    height: 0;
    position: absolute;
    ${"" /* top: 0; */}
    ${"" /* left: -100%; */}
    opacity: 0;
    transition-property: height, opacity;
    transition: 0.3s ease-in-out;

    &.menu--visible {
      visibility: visible;
      min-height: 200px;
      top: 80px;
      ${"" /* left: 0; */}
      opacity: 1;
    }
  }
`;
const NavList = styled.ul`
  display: flex;
  gap: 10px;
  list-style: none;
  text-align: center;
  width: 45%;
  justify-content: end;
  align-items: center;

  @media screen and (max-width: 800px) {
    width: 100%;
    justify-content: center;
    gap: 2rem;
    padding-left: 0; /*  overwrite bootstrap default left padding*/
  }
`;

const NavItem = styled.li``;

const NavLink = styled.a`
  color: #fff;
  text-decoration: none;
  padding: 0.5rem 1rem;
  transition: all 0.2s ease;
  opacity: 0.75;

  &:hover {
    ${"" /* background-color: #a1a1a1; */}
    border-radius: 4px;
    opacity: 1;
  }
`;

const ContactList = styled.ul`
  display: flex;
  align-items: center;
  list-style-type: none;
  padding-left: 0; /* remove bootstrap default padding left*/

  @media screen and (max-width: 800px) {
    ${"" /* position: relative; */}

    flex-direction: column;
    gap: 2em;
  }
`;

const ContactItem = styled.li`
  display: inline-block;
  margin-left: 14px;
`;

const SocialLink = styled.a`
  width: 42px;
  height: 42px;
  background: rgba(217, 217, 217, 0.1);
  display: inline-flex;
  border-radius: 50%;
  margin-right: 6px;
  align-items: center;
  justify-content: center;
  line-height: 1;
  border: 1px solid rgba(255, 255, 255, 0.5);
  &::before {
    content: "";
    width: 42px;
    height: 42px;
    position: absolute;
    background-color: #ffffff;
    border-radius: 50%;
    transform: scale(0);
    transition: 0.3s ease-in-out;
  }
  &:hover::before {
    transform: scale(1);
  }
  img {
    width: 40%;
    z-index: 1;
    transition: 0.3s ease-in-out;
  }
  &:hover img {
    filter: brightness(0) saturate(100%) invert(0%) sepia(7%) saturate(98%)
      hue-rotate(346deg) brightness(95%) contrast(86%);
  }
`;

const Navbar2 = () => {
  const [menuClicked, setMenuClicked] = useState(false);

  const handleMenuClick = () => {
    console.log(menuClicked);
    setMenuClicked(!menuClicked);
  };
  return (
    <Container>
      <NavbarItems>
        <NavbarLogo>Carl</NavbarLogo>
        <MenuIcon onClick={() => handleMenuClick()}>
          <img
            src={menuClicked ? CloseIcon : HamburgerIcon}
            alt={menuClicked ? "close menu" : "open menu"}
          />
        </MenuIcon>

        <Menu className={menuClicked ? "menu--visible" : ""}>
          <NavList>
            {MenuItems.map((item, index) => (
              <NavItem>
                <NavLink href={item.url} key={index}>
                  {item.title}
                </NavLink>
              </NavItem>
            ))}
          </NavList>

          <ContactList className={menuClicked ? "contact--visible" : ""}>
            <div>
              {SocialLinks.map((sociallink, index) => (
                <ContactItem>
                  <SocialLink href={sociallink.url} key={index}>
                    <img src={sociallink.imageSrc} alt={sociallink.altText} />
                  </SocialLink>
                </ContactItem>
              ))}
            </div>

            <ContactItem>
              <Button
                buttonContent={
                  <a
                    href="#connect"
                    style={{ color: "inherit", textDecoration: "none" }}
                  >
                    Let's connect
                  </a>
                }
                // TODO:create a link button instead of wrapping an anchor tag in button
                animationType="background-circle"
                accentColor="#fff"
                accentTextColor="#000"
                backgroundColor="#000"
                color="#fff"
              ></Button>
            </ContactItem>
          </ContactList>
        </Menu>
      </NavbarItems>
    </Container>
  );
};

export default Navbar2;
