import { Col, Container, Row } from "react-bootstrap";

import Logo from "../assets/icons/logo-1.png";
import FacebookLogo from "../assets/icons/social_facebook_icon.svg";
import GithubLogo from "../assets/icons/social_facebook_icon.svg";
import TwitterLogo from "../assets/icons/social_twitter_icon.svg";
import LinkedInLogo from "../assets/icons/social_linkedin.svg";

const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          <Col sm={6}>
            <img src={Logo} alt="Logo" />
          </Col>
          <Col sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href=" ">
                <img src={FacebookLogo} alt="" />
              </a>
              <a href=" ">
                <img src={GithubLogo} alt="" />
              </a>
              <a href=" ">
                <img src={LinkedInLogo} alt="" />
              </a>
              <a href=" ">
                <img src={TwitterLogo} alt="" />
              </a>
            </div>
            <p>CopyRight 2022, All Right Reserved</p>
          </Col>
        </Row>
      </Container>
    </footer>
  );
};

export default Footer;
