import { useState, useEffect } from "react";

import { Col, Container, Row } from "react-bootstrap";
import headerImg from "../assets/img/header-img.svg";

const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);

  const toRotate = ["FrontEnd Web Developer", "Backend Web Developer"];

  const [isDeleting, setIsDeleting] = useState(false);

  const [text, setText] = useState("");

  const [delta, setDelta] = useState(300 - Math.random() * 100);

  const period = 2000;

  const tick = () => {
    let i = loopNum % toRotate.length;

    let fullText = toRotate[i];

    let updatedText = isDeleting
      ? fullText.substring(0, text.length - 1)
      : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta((prevDelta) => prevDelta / 2);
    }

    if (isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setDelta(period);
    } else if (isDeleting && updatedText === "") {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setDelta(500);
    }
  };

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => {
      clearInterval(ticker);
    };
  }, [text]);

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="align-items-center">
          <Col xs={12} md={6} xl={7}>
            <span className="tagline">Welcome to my portfolio</span>
            <h1>
              {`Hi I'm  Charles `}
              <span className="wrap">{text}</span>
            </h1>
            <p>
              Lorem ipsum, dolor sit amet consectetur adipisicing elit.
              Veritatis dolorum adipisci ratione tempore ullam rem numquam fugit
              autem, temporibus sed unde, asperiores cumque modi minus.
            </p>

            <button onClick={() => console.log("connect")}></button>
          </Col>

          <Col xs={12} md={6} xl={5}>
            <img src={headerImg} alt="" />
          </Col>
        </Row>
      </Container>
    </section>
  );
};
export default Banner;
