import React from "react";
import styled from "styled-components";
import Logo from "./assets/icons/logo-1.png";

const DetailsContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 2.5em 6px 0 6px;
  line-height: 1.4;
`;
const MediumText = styled.span`
  font-size: 18px;
  color: #fff;
  font-weight: 800;
  text-transform: uppercase;
`;
const SmallText = styled.span`
  font-size: 11px;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
`;
// const Stack  = styled.span`
//   font-size:18px;
//   color: #fff;
//   font-weight: 700;
//   text-transform:uppercase;
// `

const SpaceHorizontalContainer = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

const Marginer = styled.div`
  margin: 1em 0;
  direction: vertical;
`;

const ViewProjectButton = styled.button`
  padding: 10px 16px;
  background-color: ${(props) => props.primaryColor || "#fbbe01"};
  color: #000;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: 700;
  border: 3px solid transparent;
  outline: none;
  cursor: pointer;
  transition: all 200ms ease-in-out;
  border-radius: 8px;

  &:hover {
    background-color: transparent;
    color: #fff;
    border: 3px solid ${(props) => props.primaryColor || "#fbbe01"};
  }
`;

const CarlLogo = styled.div`
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;

  img {
    width: auto;
    height: 30px;
  }
`;
const ProjectDetails = (props) => {
  return (
    <DetailsContainer>
      <SmallText>{props.project.projectStack}</SmallText>
      <SpaceHorizontalContainer>
        <MediumText>{props.project.projectName}</MediumText>
        <MediumText>{props.project.projectType}</MediumText>
      </SpaceHorizontalContainer>
      <Marginer />
      <SpaceHorizontalContainer>
        <MediumText>{props.project.projectShortDescp}</MediumText>
        <ViewProjectButton primaryColor={props.project.primaryColor}>
          VIEW
        </ViewProjectButton>
      </SpaceHorizontalContainer>

      <CarlLogo>
        <img src={Logo} alt="" />
      </CarlLogo>
    </DetailsContainer>
  );
};
export default ProjectDetails;
