import React from "react";
import styled from "styled-components";
import { css } from "styled-components";

const StyledButton = styled.button`
  box-sizing: border-box;

  background-color: ${(props) => props.backgroundColor || "#E3E3E3"};
  color: ${(props) => props.color || "#000"};
  padding: 0.5em 1em;
  border: none;
  outline: none;
  position: relative;
  cursor: pointer;

  ${(props) => {
    switch (props.animationType) {
      case "border-pop":
        return css`
          &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: -1;
            border: ${(props) => props.borderSize || "2px"} solid
              ${(props) => props.backgroundColor || "#E3E3E3"};
            transition: top, left, right, bottom, 100ms ease-in-out;
            ${"" /* border: 2px solid green; */}
          }
          &:hover::before,
          &:focus::before {
            top: calc(${(props) => props.borderSize || "2px"} * -2);
            right: calc(${(props) => props.borderSize || "2px"} * -2);
            bottom: calc(${(props) => props.borderSize || "2px"} * -2);
            left: calc(${(props) => props.borderSize || "2px"} * -2);
          }
        `;
      case "background-slide":
        return css`
          &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background-color: ${(props) => props.accentColor};
            z-index: -1;

            transition: transform 300ms ease-in-out;

            transform: scaleX(0);
            transform-origin: left;
          }

          &:hover::before,
          &:focus::before {
            transform: scaleX(1);
          }

          & {
            ${
              "" /* messes with the stacking context so both the before pseudo element and the element are visible */
            }
            z-index: 1;
            transition: color 300ms ease-in-out;
          }

          &:hover,
          &:focus {
            color: ${(props) => props.accentTextColor};
          }
        `;
      case "background-circle":
        return css`
          &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background-color: ${(props) => props.backgroundColor};
            z-index: -1;

            border-radius: 50%;

            transform: scale(1.5);

            transition: transform 300ms ease-in-out;
          }

          & {
            z-index: 1;
            ${"" /* hide the excess from the overscaled b4 elem */}
            overflow: hidden;
            background-color: ${(props) => props.accentColor};

            transition: color 300ms ease-in-out;
          }

          &:hover {
            color: ${(props) => props.accentTextColor};
          }

          &:hover::before,
          &:focus::before {
            transform: scale(0);
          }
        `;
      case "border-underline":
        return css`
          &::before {
            content: "";
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            height: ${(props) => props.borderSize || "4px"};
            background-color: ${(props) => props.accentColor};

            transform: scaleX(0);
            transition: transform 300ms ease-in-out;
          }

          &:hover::before,
          &:focus::before {
            transform: scaleX(1);
          }
        `;
      default:
        return css``;
    }
  }}
`;

// const ButtonBorderPop = styled(Button)``;

const Button = (props) => {
  return (
    <StyledButton
      backgroundColor={props.backgroundColor}
      color={props.color}
      animationType={props.animationType}
      // style={{ margin: "20px 50px" }}
      accentColor={props.accentColor}
      accentTextColor={props.accentTextColor}
      borderSize={props.borderSize}
    >
      {props.buttonContent}
    </StyledButton>
  );
};

export default Button;
