import styled from "styled-components";
import { Container } from "react-bootstrap";
// import { useState } from "react";

import ProjectCard from "./ProjectCard/ProjectCard";
import Project1Image from "../assets/img/project_1.jpg";
import Project2Image from "../assets/img/project_2.jpg";
import Project3Image from "../assets/img/project_3.jpg";
// import { AnimatePresence } from "framer-motion";

// import { selectedId } from "./ProjectCard/ProjectCard";

const Project1 = {
  projectName: "easyBank",
  projectType: "landing page",
  projectStack: "HTML/CSS/JavaScript",
  projectShortDescp: "lorem ipsum sit",
  primaryColor: "hsl(233, 26%, 24%)",
  projectImage: Project1Image,
  projectId: 1,
};

const Project2 = {
  projectName: "Shortly",
  projectType: "Web app",
  projectStack: "HTML/CSS/JavaScript/API",
  projectShortDescp: "shorten your links",
  primaryColor: "hsl(255, 11%, 22%);",
  projectImage: Project2Image,
  projectId: 2,
};

const Project3 = {
  projectName: "R-P-S",
  projectType: "Web App",
  projectStack: "HTML/CSS/JavaScript",
  projectShortDescp: "rock paper scissors lizard spock",
  primaryColor: "red",
  projectImage: Project3Image,
  projectId: 3,
};

const ProjectsContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  & > * {
    flex-basis: 300px;
  }
`;

const Projects = () => {
  const projects = [Project1, Project2, Project3];

  // const [selectedId, setSelectedId] = useState(null);

  const ProjectCards = projects.map((project) => (
    <ProjectCard project={project} key={project.projectId} />
  ));
  return (
    <section style={{ marginTop: "4em", marginBottom: "4em" }}>
      <Container>
        <h1>My Projects</h1>
        <ProjectsContainer>{ProjectCards}</ProjectsContainer>

        {/* <AnimatePresence>
          {selectedId && <ProjectCard layoutId={selectedId} />}
        </AnimatePresence> */}
      </Container>
    </section>
  );
};

export default Projects;
