import { motion, useMotionValue, useTransform, Variants } from "framer-motion";
import React from "react";

import styled from "styled-components";

// import Project1Image from "./assets/img/project_1.jpg";
import ProjectDetails from "./ProjectDetails";

const CardWrapper = styled(motion.div)`
  width: 100%;
  perspective: 2000;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2em;
`;
const CardContainer = styled(motion.div)`
  width: 285px;
  height: 500px;
  display: flex;
  flex-direction: column;
  border-radius: 25px;
  box-shadow: 0 2px 7px 1px rgba(31, 31, 31, 0.2);
  background-color: #1d1f21;
  color: #fff;
  position: relative;
  cursor: grab;
  ${"" /* background-color: */}
`;

const CircleContainer = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  overflow: hidden;
  border-top-right-radius: 25px;
`;

const Circle = styled(motion.div)`
  position: absolute;
  width: 350px;
  height: 350px;
  top: -4.2em;
  right: -10em;
  z-index: 5;
  background-color: ${(props) => props.primaryColor || "#fbbe01"};
  border-radius: 50%;
`;

const TopContainer = styled(motion.div)`
  width: 100%;
  display: flex;
  flex-direction: column;
  flex: 1.2;
  position: relative;
  align-items: center;
  justify-content: flex-end;
  padding: 1em 15px;
`;

const BottomContainer = styled(motion.div)`
  display: flex;
  flex: 0.8;
  padding: 0 1em;
`;

const ProjectName = styled(motion.h3)`
  color: #fff;
  text-transform: uppercase;
  margin: 0;
  z-index: 10;
  font-size: 45px;
  font-weight: 900;
`;

const ProjectWrapper = styled(motion.div)`
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Project = styled(motion.div)`
  width: auto;
  height: 190px;
  z-index: 99;
  user-select: none;
  img {
    width: auto;
    height: 100%;
    user-select: none;
  }
`;

// let selectedId, setSelectedId;
const ProjectCard = (props) => {
  // for card open animation
  // const [selectedId, setSelectedId] = useState(null);
  //

  const x = useMotionValue(0);
  const y = useMotionValue(0);
  const rotateX = useTransform(y, [-100, 100], [30, -30]);
  const rotateY = useTransform(x, [-100, 100], [30, -30]);

  const imageAnimate = {
    offscreen: { x: -100, opacity: 0 },
    onscreen: {
      x: 0,
      opacity: 1,
      rotate: [0, 10, 0],
      transition: {
        type: "spring",
        bounce: 0.5,
        duration: 1,
      },
    },
  };

  const textAnimate = {
    offscreen: { y: 100, opacity: 0 },
    onscreen: {
      y: 0,
      opacity: 1,
      // rotate: [0, 10, 0],
      transition: {
        type: "spring",
        bounce: 0.5,
        duration: 1,
      },
    },
  };

  return (
    <CardWrapper
      //
      //
      // fade on scroll anmimation
      //
      //
      initial={{ opacity: 0 }}
      whileInView={{ opacity: 1 }}
      transition={{ duration: 1 }}
      viewport={{ once: false }}
      // layoutId={props.project.projectId}
      // onClick={() => setSelectedId(props.project.projectId)}
    >
      <CardContainer
        // 3D Animation
        style={{ x, y, rotateX, rotateY, z: 100 }}
        drag
        dragElastic={0.16}
        dragConstraints={{ top: 0, left: 0, right: 0, bottom: 0 }}
        whileTap={{ cursor: "grabbing" }}
        //
        //
        // for staggered AOS animation
        //
        //

        initial={"offscreen"}
        whileInView={"onscreen"}
        viewport={{ once: false, amount: 0.25 }}
        transition={{ staggerChildren: 0.5 }}
      >
        <TopContainer>
          <CircleContainer>
            <Circle primaryColor={props.project.primaryColor} />
          </CircleContainer>
          <ProjectWrapper>
            <Project
              // 3D card animation
              style={{ x, y, rotateX, rotateY, z: 100000 }}
              drag
              dragElastic={0.12}
              whileTap={{ cursor: "grabbing" }}
              // Scroll triggered animation
              // initial={"offscreen"}
              // animate={"onscreen"}
              variants={imageAnimate}
            >
              <img src={props.project.projectImage} alt="" />
            </Project>
          </ProjectWrapper>
          <ProjectName
            // initial={"offscreen"}
            // animate={"onscreen"}
            variants={textAnimate}
          >
            {props.project.projectName}
          </ProjectName>
        </TopContainer>
        <BottomContainer
          // initial={"offscreen"}
          // animate={"onscreen"}
          variants={textAnimate}
        >
          <ProjectDetails project={props.project} />
        </BottomContainer>
      </CardContainer>
    </CardWrapper>
  );
};

export default ProjectCard;
// export { selectedId };
